*
{
    margin:0;
    padding:0;
}
body
{
    background-color: rgb(168, 168, 76);
    font-size: "Bree Serif",serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.heading
{
    text-align: center;
    color: #f71aa2;
}
.app
{
    background: linear-gradient(83deg,#f71aa2 0%,#6800ff 80%);
    width: 95%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0.75);
    display: flex;
    justify-content: space-around;
}
.Question-Section h1 span
{
    color: red;
    font-size: 40px;
    margin-bottom: 10px;
}
.Question-Text
{
    font-size: 30px;
    margin-top: 10px;
    color: rgb(96, 249, 71);
}
.Answer-Section
{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
}
button
{
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    background: linear-gradient(83deg,#f71aa2 0%,#6800ff 80%);
    border-radius: 15px;
    display: flex;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;
    border: 3px solid #234668;
    cursor: pointer;
}
.score-section
{
    display: flex;
    align-items: center;
    font-size: 30px;
    color: white;
    font-weight: bold;
}